<template>
  <form class="ly-form" @submit.prevent="onSubmit" @reset="onReset">
    <slot></slot>
  </form>
</template>

<script>
import { inArray } from '../../helpers/util'
import { CustomEvent } from '../../helpers/events'

export default {
  name: 'ly-form',
  props: {},
  data() {
    return {}
  },
  computed: {},
  watch: {},
  created() {},
  ready() {},
  mounted() {},
  updated() {},
  attached() {},
  methods: {
    onSubmit(e) {
      const inputEls = e.target.elements
      const value = {}
      const uids = []

      inputEls.forEach(el => {
        const _ac = el._app_component
        if (
          _ac &&
          _ac._uid &&
          !inArray(_ac._uid, uids) // checbox
        ) {
          // 
          uids.push(_ac._uid)
          if (_ac.name) {
            value[_ac.name] = _ac.hookFormValue
              ? _ac.hookFormValue()
              : _ac.formValue
          }
        } else {
          // 
          if (el.name) {
            const { type, name, checked } = el

            if (type === 'checkbox') {
              // 
              if (!value[name]) {
                value[name] = []
              }

              if (checked) {
                value[name].push(el.value)
              }
            } else if (type === 'radio') {
              if (value[name] == null) {
                value[name] = ''
              }
              // checked
              if (checked) {
                value[name] = el.value
              }
            } else if (type === 'select-multiple') {
              // 
              const selectOptions = el.options
              const selectedValues = []
              for (let i = 0; i < selectOptions.length; i++) {
                // optionvalue
                if (selectOptions[i].selected) {
                  selectedValues.push(selectOptions[i].value)
                }
              }
              value[name] = selectedValues
            } else if (
              type === 'input' ||
              type === 'select-one' ||
              type === 'hidden'
            ) {
              // input
              value[name] = el.value
            }
          }
        }
      })

      this.$emit(
        e.type,
        new CustomEvent(e, {
          value
        })
      )

      return false
    },

    onReset(e) {
      const inputEls = e.target.elements
      const value = {}
      const uids = []

      inputEls.forEach(el => {
        const _ac = el._app_component

        if (
          _ac &&
          _ac._uid &&
          !inArray(_ac._uid, uids) // checbox
        ) {
          // 
          uids.push(_ac._uid)

          if (_ac.reset) {
            _ac.reset()
          }
        } else {
          // 
        }
      })

      this.$emit(
        e.type,
        new CustomEvent(e, {
          value
        })
      )
    }
  }
}
</script>
